.App {
  font-family: sans-serif;
  text-align: center;
}
body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

div {
  background-image: url("https://3.bp.blogspot.com/-pNz44UfwDxk/Vn-CnuX8adI/AAAAAAAA2L8/f1C7p7wm8k0/s800/eto_remake_hitsuji.png");
}

.button1 {
  position: absolute;
  top: 80%;
  left: 20%;
  border: 1px solid #999;
  line-height: 100px;
  height: 100px;
  width: 100px;
}
